.search-tip {
  position: relative;
  height: auto;
}
.search-tip .results {
  position: absolute;
  left: 0px;
  top
  height: auto;
  max-height: 120px;
  width: 250px;
  padding: 5px 0;
  background: #fff;
  border: 1px solid #e4e4e4;
  z-index: 100;
}
.search-tip .results.show {
  display: block;
}
.search-tip .results .item {
  height: 30px;
  padding: 0 8px;
  line-height: 30px;
  font-size: 1em;
  cursor: pointer;
  color: #333;
}
.search-tip .results .item:hover {
  background: #eaeefb;
}
.search-tip .results .item:active {
  background: #468df1;
  color: #fff;
}




