
.refresh {
  background: #fff;
  overflow-y: scroll;
}
.refresh::-webkit-scrollbar {
  width: 8px;
}
.refresh::-webkit-scrollbar-track {
  background-color: #fff;
}
.refresh::-webkit-scrollbar-thumb {
  border-radius: 0px;
  background: #b4b4b4;
}



