
.select {
  display: inline-block;
  position: relative;
}
.arrow-bottom {
  display: inline-block;
  width: 0;
  height: 0;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 5px solid #ddd;
}
.drop-option {
  display: block;
  position: absolute;
  top: 30px;
  left: 0;
  background: #fff;
  min-width: 120px;
  overflow-y: auto;
  z-index: 1000;
  text-align: left;
  box-shadow: 0 1px 6px rgba(0,0,0,.117647), 0 1px 4px rgba(0,0,0,.117647);
  cursor: pointer;
}
.drop-option>li {
  list-style: none;
  padding: 7px 25px 7px 20px;
  color: #555;
}
.drop-option>li:hover {
  background: #f5f5f5;
}
.drop-option .active {
  color: #468df1;
}


/*动画效果*/
.fade-enter-active {
    transition: all .3s ease;
}
.fade-leave-active {
    transition: all .3s ease;
}
.fade-enter, .fade-leave-active {
    transform: translateY(-6px);
    opacity: 0;
}



